
  /*	OVERRIDING DEFAULTS
  ----------------------------------------------------- */
.prx-timeline-embed {
  border-bottom: 3px solid $orange;

  .tl-timeline,
  .tl-story-slider {
    font-family: $header-font-family;
    background: transparent;
  }

  .timeline-embed
  .tl-timeline {
    font-family: $header-font-family;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.3em;
  }

  .tl-timeline h1,
  .tl-timeline h2,
  .tl-timeline h3,
  .tl-timeline h4,
  .tl-timeline h5,
  .tl-timeline h6 {
    font-family: $header-font-family;
    text-transform: none;
    font-weight: 700;
    font-size: 46px;
    line-height: 46px;
    text-shadow: none;
  }

  .tl-full-image-background .tl-text,
  .tl-full-color-background .tl-text,
  .tl-full-image-background .tl-text p,
  .tl-full-color-background .tl-text p,
  .tl-slidenav-previous .tl-slidenav-content-container.tl-slidenav-inverted .tl-slidenav-icon, .tl-slidenav-next .tl-slidenav-content-container.tl-slidenav-inverted .tl-slidenav-icon, .tl-slidenav-previous .tl-slidenav-content-container.tl-slidenav-inverted .tl-slidenav-title,
  .tl-slidenav-next .tl-slidenav-content-container.tl-slidenav-inverted .tl-slidenav-title,
  .tl-slidenav-previous .tl-slidenav-content-container.tl-slidenav-inverted .tl-slidenav-description,
  .tl-slidenav-next .tl-slidenav-content-container.tl-slidenav-inverted .tl-slidenav-description {
    text-shadow: none;
  }

  .tl-slidenav-previous .tl-slidenav-content-container .tl-slidenav-icon, .tl-slidenav-next .tl-slidenav-content-container .tl-slidenav-icon, .tl-slidenav-previous .tl-slidenav-content-container .tl-slidenav-title, .tl-slidenav-next .tl-slidenav-content-container .tl-slidenav-title, .tl-slidenav-previous .tl-slidenav-content-container .tl-slidenav-description, .tl-slidenav-next .tl-slidenav-content-container .tl-slidenav-description {
    text-shadow: none;
    color: $white;
  }

  .tl-slidenav-previous .tl-slidenav-title, .tl-slidenav-next .tl-slidenav-title {
    opacity: 0.8;
  }

  .tl-timeline h2.tl-headline-title {
    font-size: 2.25rem;
    line-height: 1.2;
    letter-spacing: -0.1rem;
  }

  .tl-timeline p {
    font-family: $header-font-family;
    font-size: 1.25rem;
    line-height: 1.5;
  }

  .tl-timeline ul {
    font-family: $header-font-family;
  }

  .tl-timeline .tl-media .tl-media-wikipedia h4 a,
  .tl-timeline .tl-media h4,
  .tl-timeline .tl-media h4 a {
    font-family: $header-font-family;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
  }

  .tl-timeline .tl-caption,
  .tl-timeline .tl-credit {
    font-family: $header-font-family;
  }

  .tl-timeline .vcard {
    font-family: $header-font-family;
    font-weight: normal !important;
  }

  .tl-timeline .tl-slidenav-next,
  .tl-timeline .tl-slidenav-previous,
  .tl-timeline .tl-message,
  .tl-timeline .tl-timegroup-message {
    font-family: $base-font-family;
    font-weight: 700;
    text-transform: none;
  }

  .tl-timeline .tl-slidenav-next .tl-slidenav-title,
  .tl-timeline .tl-slidenav-previous .tl-slidenav-title,
  .tl-timeline .tl-message .tl-slidenav-title,
  .tl-timeline .tl-timegroup-message .tl-slidenav-title {
    line-height: 1.3em !important;
  }
  .tl-timeline .tl-headline-date,
  .tl-timeline h3.tl-headline-date {
    font-family: $header-font-family !important;
    font-weight: normal !important;
    text-transform: none !important;
    font-size: 2rem;
    font-style: italic;
    margin-bottom: 0.5rem;
  }
  .tl-timeline .tl-headline-date small,
  .tl-timeline h3.tl-headline-date small {
    font-weight: normal !important;
  }
  .tl-timeline .tl-timenav-slider {
    font-family: $header-font-family;
    font-weight: 700;
  }
  .tl-timeline .tl-timenav-slider h2.tl-headline {
    font-family: $header-font-family;
    font-weight: normal;
    letter-spacing: normal;
    text-transform: none;
  }
  .tl-timeline .tl-timenav-slider .tl-timeaxis {
    font-family: $header-font-family;
    font-weight: 700;
    text-transform: none;
  }
  .tl-timeline .tl-menubar {
    font-family: $header-font-family;
  }
  .tl-timeline blockquote,
  .tl-timeline blockquote p {
    font-family: $header-font-family;
    font-style: normal;
    font-weight: 400;
    line-height: 1.3;
  }
  input[type="text"].editor-headline {
    font-family: $header-font-family;
    text-transform: none;
    font-weight: 700;
    font-size: 46px;
    line-height: 46px;
  }

  .tl-slide {
    &.tl-slide-titleslide {
      background-image: linear-gradient(90deg, rgba(10,98,141,1) 18%, rgba(2,0,36,1) 60%, rgba(0,58,87,1) 100%);
      
      .tl-headline,
      .tl-headline-date {
        color: $white;
      }
    }
  }  
}
